// Отримуємо контейнер галереї
const galleryEl = document.querySelector(".gallery");

// Створюємо HTML-розмітку для галереї
const galleryMarkup = images
  .map(
    ({ preview, original, description }) => `
      <li class="gallery-item">
        <a class="gallery-link" href="${original}">
          <img 
            class="gallery-image" 
            src="${preview}" 
            alt="${description}" 
          />
        </a>
      </li>
    `
  )
  .join("");

// Додаємо розмітку в DOM
galleryEl.innerHTML = galleryMarkup;

// Ініціалізуємо SimpleLightbox
const lightbox = new SimpleLightbox(".gallery a", {
  captionsData: "alt",    // Використовуємо атрибут alt для підписів
  captionDelay: 250,      // Затримка перед показом підпису (250 мс)
  overlayOpacity: 0.8,    // Трохи затемнюємо фон
});
